import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule } from '@ionic/storage';
import { NativePageTransitions } from '@ionic-native/native-page-transitions';

import { MyApp } from './app.component';
import { AuthentificationProvider } from '../providers/authentification/authentification';
import { ApiProvider } from '../providers/api/api';
import { LignesProvider } from '../providers/lignes/lignes';
import { MediaProvider } from '../providers/media/media';
import { NotesProvider } from '../providers/notes/notes';
import { TypeNoteProvider } from '../providers/type-note/type-note';

import { Camera } from '@ionic-native/camera';
import { FileTransfer } from '@ionic-native/file-transfer';

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
		HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/configuration/configuration.module.ngfactory#ConfigurationPageModuleNgFactory', name: 'ConfigurationPage', segment: 'configuration', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/liste-note/liste-note.module.ngfactory#ListeNotePageModuleNgFactory', name: 'ListeNotePage', segment: 'liste-note', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/single-ligne/single-ligne.module.ngfactory#SingleLignePageModuleNgFactory', name: 'SingleLignePage', segment: 'single-ligne', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/single-note/single-note.module.ngfactory#SingleNotePageModuleNgFactory', name: 'SingleNotePage', segment: 'single-note', priority: 'low', defaultHistory: [] }
  ]
}),
		IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthentificationProvider,
    ApiProvider,
    LignesProvider,
    MediaProvider,
    NotesProvider,
    TypeNoteProvider,
		NativePageTransitions,
		Camera,
		FileTransfer
  ]
})
export class AppModule {}



